@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  /* not supported by: IE (7,8)
   	and only partially supported by: Opera Mini (all)
  */
  background-image: none;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  list-style-type: none;
  font-size: 20px;
  font-size: 30px;
  font-weight: 100;
  line-height: 100%;
}
@media (max-width: 1004px) {
  * {
    font-size: calc(10px + 10 * ((100vw - 320px) / 684));
  }
}
@media (min-width: 1004px) {
  * {
    font-size: calc(20px + 10 * ((100vw - 320px) / 2240));
  }
}
*, *:after, *:before {
  transition: all 0.3s linear 0s;
}

html {
  scroll-behavior: smooth;
}

a, button, input, textarea, form {
  color: #000;
  text-decoration: none;
  border: 1px solid #000;
  /*&:hover {
  	cursor: pointer;
  }*/
}

span {
  font-size: 100%;
}

img {
  /* Не поддерживаеться Internet Explorer*/
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

