*.lock {
  overflow: hidden;
}

#wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1AC2B8;
}

#content {
  display: block;
  margin: 0 auto;
  max-width: 1004px;
  padding: 20px;
}
@media (max-width: 1004px) {
  #content {
    padding: calc(0px + 20 * ((100vw - 320px) / 684));
  }
}

.anim-elem {
  -webkit-transition: margin, padding 0s linear;
  -moz-transition: margin, padding 0s linear;
  -ms-transition: margin, padding 0s linear;
  -o-transition: margin, padding 0s linear;
  transition: margin, padding 0s linear;
  -webkit-transform: translate(0, -100%);
  -moz-transform: translate(0, -100%);
  -ms-transform: translate(0, -100%);
  -o-transform: translate(0, -100%);
  transform: translate(0, -100%);
  opacity: 0;
  -webkit-transition: opacity 1s ease-in-out 0s, transform 1s ease-in-out 0s;
  -moz-transition: opacity 1s ease-in-out 0s, transform 1s ease-in-out 0s;
  -ms-transition: opacity 1s ease-in-out 0s, transform 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s, transform 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s, transform 1s ease-in-out 0s;
}
.anim-elem.active-fx {
  transform: translate(0px, 0px);
  opacity: 1;
}